'use client';
import { useEffect, useState } from 'react';
import { createClientComponentClient } from '@supabase/auth-helpers-nextjs';
import { motion, AnimatePresence } from 'framer-motion';

export default function IntelFeed() {
  const [intel, setIntel] = useState<any[]>([]);
  const [pulse, setPulse] = useState(false); // üß† A.I.V.E. heartbeat pulse
  const supabase = createClientComponentClient();

  useEffect(() => {
    fetchIntel();

    const channel = supabase
      .channel('intel-updates')
      .on('postgres_changes', { event: '*', schema: 'public', table: 'intel' }, (payload) => {
        console.log('üß© New intel received:', payload.new);
        setIntel((prev) => [payload.new, ...prev]);
        triggerPulse(); // üíõ Trigger heartbeat on new intel
      })
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, []);

  const fetchIntel = async () => {
    const { data, error } = await supabase
      .from('intel')
      .select('*')
      .order('created_at', { ascending: false })
      .limit(10);

    if (error) console.error('Error fetching intel:', error);
    else setIntel(data);
  };

  const triggerPulse = () => {
    setPulse(true);
    setTimeout(() => setPulse(false), 1500);
  };

  return (
    <div className="bg-slate-900/60 p-5 rounded-2xl shadow-xl border border-slate-700 relative overflow-hidden">
      {/* Header with animated brain pulse */}
      <div className="flex justify-between items-center mb-4">
        <div className="flex items-center gap-2">
          <motion.div
            animate={{
              scale: pulse ? [1, 1.3, 1] : 1,
              boxShadow: pulse
                ? ['0 0 0px rgba(255,215,0,0)', '0 0 25px rgba(255,215,0,0.8)', '0 0 0px rgba(255,215,0,0)']
                : '0 0 0px rgba(255,215,0,0)',
            }}
            transition={{ duration: 1.5, ease: 'easeInOut' }}
            className="w-5 h-5 rounded-full bg-yellow-400 shadow-lg"
          />
          <h2 className="text-xl font-bold text-blue-400 drop-shadow-sm">
            A.I.V.E. Live Feed
          </h2>
        </div>
        <span className="text-xs font-semibold text-black bg-yellow-400 px-3 py-1 rounded-md shadow-md">
          LISTENING
        </span>
      </div>

      {/* Intel List */}
      <div className="space-y-3 max-h-[60vh] overflow-y-auto pr-2">
        <AnimatePresence>
          {intel.map((item) => (
            <motion.div
              key={item.id}
              initial={{ opacity: 0, y: 15 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0 }}
              transition={{ duration: 0.4 }}
              className="bg-slate-800/80 border border-slate-700 p-4 rounded-xl hover:bg-slate-700/60 transition-all"
            >
              <div className="flex justify-between items-center mb-1">
                <h3 className="font-semibold text-lg text-blue-300">{item.headline}</h3>
                <span
                  className={`text-xs font-bold px-2 py-1 rounded ${
                    item.sentiment_level === 'Positive'
                      ? 'bg-green-600/80 text-white'
                      : item.sentiment_level === 'Negative'
                      ? 'bg-red-600/80 text-white'
                      : 'bg-gray-600/80 text-white'
                  }`}
                >
                  {item.sentiment_level || 'Neutral'}
                </span>
              </div>
              <p className="text-gray-300 text-sm mb-2">{item.summary}</p>

              {item.insights && (
                <p className="text-gray-400 text-xs italic mb-2">
                  {item.insights}
                </p>
              )}

              <div className="flex justify-between text-xs text-gray-400 mt-2">
                <span>üåê {item.region}</span>
                <span>üí° Confidence: {item.confidence_score || 0}%</span>
                <span>üìä Impact: {item.impact_score || 0}/100</span>
              </div>
            </motion.div>
          ))}
        </AnimatePresence>
      </div>
    </div>
  );
}
